const initialState = {
  people: [],
  selectedPerson: {},
  isFetchingPeople: true,
  planets: [],
  selectedPlanet: {},
  isFetchingPlanet: true,
  starships: [],
  selectedStarship: {},
  isFetchingStarship: true,
  randomPlanet: {},
  isFetchingRandomPlanet: true
};

const reducer = (state = initialState, action) => {
  // console.log(action.type);

  switch (action.type) {
    case 'FETCH_PEOPLE':
      const updatedPeople = action.payload.map((el, i) => {
        el.id = i + 1
        return el
      });
      return {
        ...state,
        people: updatedPeople,
        selectedPerson: updatedPeople[0],
        isFetchingPeople: false
      }
    case 'SELECT_PEOPLE':
      const filteredPeople = state.people.filter(el => el.name === action.payload)
      return {
        ...state,
        selectedPerson: filteredPeople[0]
      }
    case 'FETCH_PLANETS':
      const updatePlanets =  action.payload.map((el, i) => {
        el.id = el.url.match(/([0-9]*)\/$/)[1]
        return el
      });
      return {
        ...state,
        planets: updatePlanets,
        selectedPlanet: updatePlanets[1],
        isFetchingPlanet: false,
        isFetchingRandomPlanet: false

      }
    case 'SELECT_PLANET':
      const filteredPlanets = state.planets.filter(el => el.name === action.payload)
      return {
        ...state,
        selectedPlanet: filteredPlanets[0]
      }
    case 'FETCH_STARSHIPS':
      const updateStarships = action.payload.map((el, i) => {
        el.id = el.url.match(/([0-9]*)\/$/)[1]
        return el
      })
      return {
        ...state,
        starships:updateStarships,
        selectedStarship: updateStarships[0],
        isFetchingStarship: false
      
      }
    case 'SELECT_STARSHIP':
      const filteredStarships = state.starships.filter(el => el.name === action.payload)
      return {
        ...state,
        selectedStarship: filteredStarships[0]
      }
    case 'RANDOM_PLANET':
      const randomID = Math.round(Math.random() * 10 + 1);
      const randomPlanet = state.planets[randomID]
      console.log(randomPlanet)
      return {
        ...state,
        randomPlanet: randomPlanet || state.planets[3],
        
      }
    default:
      return state
  }
};

export default reducer;